.partners {
    padding: rem(64) 0;
    background-color: var(--c-light-blue);
    h2 {
        text-align: center;
        margin-bottom: rem(48);
        // max-width: rem(325);
        // margin-left: auto;
        // margin-right: auto;
    }
    &__items {
        // padding: 0 rem(43);
        max-width: rem(246);
        margin-left: auto;
        margin-right: auto;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        .item {
            display: flex;
            flex: 0 0 50%;
            max-width: 50%;
            margin-bottom: rem(24);
            &:nth-child(2n) {
                justify-content: flex-end;
            }
            &:last-child {
                justify-content: center;
                margin-bottom: 0;
            }
            a {
                display: block;
                img {
                    transition: all 100ms ease;
                    filter: grayscale(100%);
                }
                &:hover {
                    img {
                        filter: grayscale(0%);
                    }
                }
            }
        }
    }
}